import axios from 'axios'
import qs from 'qs'
import { Message } from 'element-ui'

const instance = axios.create({
  baseURL: '/api',
  timeout: 5000,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded',
    fromway: window.innerWidth > 900 ? 'PC' : 'h5'   // 来源， 1：ios，2：android，3：PC，4：h5，5：未知
  }
})

// Add a request interceptor
instance.interceptors.request.use(config => {
  return config
}, err => {
  return Promise.reject(err)
})

// Add a response interceptor
instance.interceptors.response.use(res => {
  const { status, data: { code } } = res
  if (status === 200 && code === 200) {
    return res.data
  } else {
    return Promise.reject(res.data)
  }
}, err => {
  return Promise.reject(err)
})

const request = (method) => {
  const commonConfig = {

  }
  return (url, params = {}, config = {}) => {
    const _config = {
      ...commonConfig,
      ...config,
      url,
      method
    }
    if (method === 'get') {
      _config.params = params
    } else {
      _config.data = qs.stringify(params)
    }
    return instance(_config).then(res => {
      return [res, null]
    }, err => {
      const message = typeof err === 'object' ? err.msg : err
      Message({
        message,
        type: 'error'
      })
      return [null, err]
    })
  }
}

export const GET = request('get')
export const POST = request('post')
export const DELETE = request('delete')
export const PUT = request('put')
